.project {
	background-color: rgb(11, 9, 53);
	width: 100%;
	height: auto;
}

.project .site-box {
	color: black;
}

.project .site-box .site0 {
	display: flex;
	margin: 0 auto;
	justify-content: space-between;
}
.project .site-box h1 {
	width: 100%;
	text-align: center;
	line-height: 100px;
	color: white;
	font-weight: 900;
}
.project .site-box .bar {
	width: 60px;
	height: 0;
	border: 1px solid #fff;
	margin: 0 auto;
}

.project .site-box .site {
	width: 450px;
	height: 340px;
	background-color: white;
	margin: 40px auto 100px;
	border-radius: 20px;
	padding: 20px;
	box-sizing: border-box;
	position: relative;
	transition: all 1s;
	box-shadow: 10px 10px 20px #000;
	top: 0;
}

.project .site-box .site:hover {
	top: 10px;
}

.project .site-box .site1 img {
	width: 100px;
	height: 100px;
}
.project .site-box .site2 img {
	width: 70px;
	height: 50px;
	margin: 25px 0 23px 10px;
}
.project .site-box .site3 img {
	width: 70px;
	height: 70px;
	margin: 14px 0;
}

.project .site-box .site a {
	cursor: pointer;
	color: rgb(68, 110, 235);
}

.project .site-box .site p {
	line-height: 40px;
}

.project .site-box .site ul {
	color: black;
	top: 170px;
	left: 0;
}

.project .site-box .site ul li {
	padding: 0;
	font-weight: 450;
}

.project .site-box .site p span {
	font-weight: 700;
}

.project .project-box {
	width: 100%;
	height: auto;
	background-color: #333;
	padding-bottom: 100px;
	border-radius: 20px;
}

.project .project-box h1 {
	width: 100%;
	line-height: 100px;
	text-align: center;
	color: white;
	font-weight: 900;
}

.project .project-box .bar {
	width: 110px;
	height: 0;
	border: 1px solid white;
	margin: 0 auto;
}
.project .project-box .project-detail {
	width: 1000px;
	height: auto;
	background-color: #fff;
	margin: 50px auto;
	border-radius: 20px;
	box-shadow: 10px 10px 20px #000;
	padding: 20px 20px 50px;
	display: flex;
	flex-flow: row wrap;
}

.project .project-box .project-detail .left {
	width: 50%;
	height: 100%;
}
.project .project-box .project-detail .left .img-box {
	width: 100%;
	height: auto;
	overflow: hidden;
	border-radius: 20px;
	object-fit: cover;
	/* box-shadow: 3px 3px 20px #333; */
	/* border-radius: 20px; */
}

.project .project-box .project-detail .left .img-box img {
	width: 100%;
	height: 100%;
	margin: 0 auto;
	object-fit: cover;
}

.project .project-box .project-detail h1 {
	color: black;
	margin: 0 0 10px;
	width: 100%;
}

.project .project-box .project-detail .right {
	padding: 0 0 0 40px;
	font-weight: 500;
	width: 40%;
}

.project .project-box .project-detail .right p {
	color: black;
	font-size: 18px;
	line-height: 1.8;
}

.project .project-box .project-detail .right a {
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}

.project .project-box .project-detail .detail {
	width: 100%;
	display: flex;
	margin: 20px 0;
	border-top: 1px solid #333;
	padding-top: 20px;
	box-sizing: border-box;
}

.project .project-box .project-detail .detail .detail-left {
	width: 30%;
}
.project .project-box .project-detail .detail .detail-left h4 {
	line-height: 1.8;
}

.project .project-box .project-detail .detail .detail-right a {
	line-height: 1.8;
	color: rgb(68, 110, 235);
}
