.home {
	position: relative;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	overflow: hidden;
	z-index: 0;
}

video {
	top: 50%;
	left: 50%;
	width: 100%;
	height: 100%;
	object-fit: cover; /* 동영상 비율을 유지하며 화면을 꽉 채우기 위해 */
	transform: translate(-50%, -50%);
	position: relative;
	filter: brightness(0.6);
	z-index: 0;
}

.home .intro {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
}
.home .intro h1 {
	color: white;
	text-align: center;
	width: 100%;
	line-height: 70px;
	font-weight: 900;
	font-size: 50px;
}

.bar-1 {
	width: 50px;
	height: 0;
	border: 1px solid #fff;
	margin: 50px auto;
}

.home .intro p {
	color: white;
	width: 100%;
	text-align: center;
	font-size: 20px;
	line-height: 40px;
}
