.about {
	width: 100%;
	height: auto;
	background-color: rgb(11, 9, 53);
	color: white;
	position: relative;
	top: 0;
}

.about .about-center {
	min-width: 1500px;
	max-width: 1700px;
	margin: 0 auto;
	position: relative;
}

.about h1 {
	text-align: center;
	line-height: 100px;
}

.about .bar {
	width: 150px;
	height: 0;
	border: 1px solid #fff;
	margin: 0 auto;
}
.me {
	width: 70%;
	margin: 50px auto;
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-flow: row wrap;
	position: relative;
	z-index: 0;
}

.me .me-box {
	background-color: rgb(11, 9, 53);
	position: relative;
	transition: all 0.5s;
}

.me .me-box:hover {
	transform: scale(1.05);
}

.me .name {
	width: 33.33%;
	height: 100px;
	display: flex;
	justify-content: space-around;
	text-align: left;
}
