.skill {
	width: 100%;
	height: 600px;
	background-color: #f5d042;
	padding-bottom: 200px;
	border-radius: 20px;
}

.skill h1 {
	width: 100%;
	text-align: center;
	color: black;
}

.skill .skill-box {
	width: 70%;
	height: 100%;
	margin: 0 auto;
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
	flex-flow: column wrap;
}

.skill .skill-box .front-end {
	width: 400px;
	text-align: center;
	background-color: white;
	height: 500px;
	border-radius: 10px;
	box-shadow: 10px 10px 20px #333;
}

.skill .skill-box .front-end h3 {
	color: black;
	line-height: 50px;
	font-size: 25px;
}

.skill .skill-box .front-end .bar {
	width: 120px;
	height: 0;
	border: 1px solid black;
}

.skill .skill-box .front-end .skill-img {
	width: 90%;
	margin: 0 auto;
	display: flex;
	flex-flow: row wrap;
	justify-content: center;
}
.skill .skill-box .front-end .skill-img .html {
	width: 33.33%;
	padding: 4%;
	box-sizing: border-box;
}

.skill .skill-box .front-end .skill-img .react {
	width: 40%;
	height: 100%;
}

.skill .skill-box .front-end .skill-img .jQuery {
	width: 60%;
}

.skill .skill-box .front-end .skill-img .boot {
	width: 50%;
}
.skill .skill-box .front-end .skill-img .framer {
	width: 50%;
}
.skill .skill-box .control {
	width: 400px;
	height: 320px;
	background-color: white;
	border-radius: 10px;
	box-shadow: 10px 10px 20px #333;
}

.skill .skill-box .control h3 {
	color: black;
	line-height: 50px;
	font-size: 25px;
	text-align: center;
}

.skill .skill-box .control .bar {
	width: 190px;
	height: 0;
	border: 1px solid black;
}

.skill .skill-box .control .control-img {
	width: 90%;
	margin: 0 auto;
	display: flex;
	justify-content: center;
	flex-flow: row wrap;
}

.skill .skill-box .control .control-img .git-logo {
	width: 30%;
}
.skill .skill-box .control .control-img .git-text {
	width: 50%;
	height: 100%;
}
.skill .skill-box .control .control-img .git {
	width: 70%;
}

.skill .skill-box .adobe {
	width: 400px;
	height: 200px;
	background-color: white;
	border-radius: 10px;
	box-shadow: 10px 10px 20px #333;
}

.skill .skill-box .adobe h3 {
	width: 100%;
	font-size: 25px;
	color: black;
	text-align: center;
	line-height: 50px;
}

.skill .skill-box .adobe .bar {
	width: 80px;
	height: 0;
	margin: 0 auto;
	border: 1px solid black;
}

.skill .skill-box .adobe .adobe-box {
	width: 90%;
	margin: 0 auto;
	display: flex;
	justify-content: center;
}

.skill .skill-box .adobe .adobe-box img {
	width: 100px;
	padding: 5%;
}

.skill .skill-box .box:hover {
	top: 10px;
}
.skill .skill-box .box {
	position: relative;
	top: 0;
	transition: all 0.5s;
}
