@media only screen and (max-width: 768px) and (min-width: 0) {
	.wrap {
		width: 100%;
		height: auto;
	}

	.center {
		max-width: none;
		min-width: 320px !important;
	}
	.home {
		position: relative;
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		overflow: hidden;
		z-index: 0;
	}
	.home .intro {
		width: 100%;
		height: 100%;
	}

	.home .intro .name {
		font-size: 25px;
	}
	.home .intro .txt {
		font-size: 23px;
	}
	.home .intro .bar-1 {
		margin: 30px auto;
	}
	.home .intro p {
		font-size: 15.3x;
	}

	.about {
		min-width: 320px;
		width: 90%;
		height: auto;
		margin: 0 auto;
		overflow: hidden;
		min-width: none;
		max-width: none;
	}
	.about-center {
		width: 100%;
		height: auto;
		max-width: 768px !important;
		min-width: 0 !important;
	}

	.about-center {
		width: 100%;
		text-align: center;
	}
	.about-center .me {
		width: 100%;
		display: flex;
		justify-content: space-around;
	}

	.about-center .me .me-box {
		height: 100px;
		width: 50%;
		display: flex;
		justify-content: space-around;
	}
	.about-center .me .me-box .left {
		width: 20%;
		display: flex;
		flex-flow: column nowrap;
		justify-content: center;
	}
	.about-center .me .me-box .left img {
		width: 90%;
		margin: 0 auto;
	}

	.about-center .me .me-box .right {
		width: 70%;
		display: flex;
		margin: 0 auto;
		flex-flow: column nowrap;
		justify-content: center;
	}
	.about-center .me .me-box .right h3 {
		font-size: 15px;
	}
	.about-center .me .me-box .right p {
		font-size: 14 px;
	}

	.skill {
		width: 100%;
		min-width: none;
		max-width: none;
		height: 1100px;
	}
	.skill h1 {
		width: 100%;
	}
	.skill .box {
		width: 100%;
		height: 1000px;
		display: flex;
		flex-flow: column nowrap;
	}

	.skill .skill-box {
		width: 100%;
		height: auto;
		display: flex;
		flex-flow: column nowrap;
	}

	.skill .skill-box .box {
		width: 90% !important;
		height: auto;
		margin: 20px auto;
	}
	.skill .skill-box .front-end h3 {
		width: 100%;
	}

	.skill .skill-box .front-end .skill-img {
		width: 100%;
		display: flex;
		overflow: row wrap;
	}
	.skill .skill-box .front-end .skill-img .react {
		width: 50%;
		height: 30%;
		object-fit: contain;
		padding: 20px;
		box-sizing: border-box;
	}
	.skill .skill-box .front-end .skill-img .jQuery {
		width: 50%;
		object-fit: contain;
	}

	.skill .skill-box .adobe .adobe-img {
		width: 33.33%;
	}

	.skill .skill-box .adobe .adobe-img img {
		width: 100%;
	}

	.project {
		width: 90%;
		margin: 0 auto;
		max-width: 100%;
	}

	.project .site-box {
		width: 100%;
		margin: 0 auto;
	}
	.project .site-box .site0 {
		width: 100%;
		flex-flow: column nowrap;
		justify-content: center;
		align-items: center;
	}
	.project .site-box .site0 .site {
		text-align: center;
		width: 90%;
		margin: 40px auto;
	}
	.project .site-box .site0 .site a {
		width: 100%;
		text-overflow: ellipsis;
		overflow: hidden;
	}

	.project .site-box .site0 .site ul {
		width: 100%;
		justify-content: center;
	}
	.project .site-box .site0 .site ul li {
		text-align: center;
		width: 100%;
	}
	.project .site-box .site0 .site p {
		width: 100%;
		text-align: center;
		display: block;
	}
	.project {
		width: 100%;
	}
	.project .project-box {
		width: 100%;
	}
	.project .project-box .project-detail {
		width: 90%;
		height: auto;
		margin: 50px auto;
		box-sizing: border-box;
		display: flex;
		flex-flow: column nowrap;
	}
	.project .project-box .project-detail .left {
		width: 100%;
	}

	.project .project-box .project-detail .left .img-box {
		width: 100%;
		height: 400px;
		/* box-sizing: border-box; */
		overflow: hidden;
		margin: 10px auto;
	}
	.project .project-box .project-detail .left .img-box img {
		width: 100%;
		height: 400px;
		object-fit: cover;
		object-position: top;
	}

	.project .project-box .project-detail .right {
		width: 100%;
		margin: 0;
		padding: 0;
	}

	.project .project-box .project-detail .right p {
		width: 100%;
		margin: 20px auto;
	}
	nav {
		display: none;
	}
}
