@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap");
* {
	margin: 0;
	padding: 0;
	font-family: "Noto Sans KR", sans-serif;
	font-optical-sizing: auto;
	font-style: normal;
}

html,
body {
	width: 100%;
	height: 100vh;
	position: relative;
	background-color: rgb(11, 9, 53);
}

a {
	text-decoration: none;
	display: block;
	color: #333;
}

ul,
ol {
	list-style: none;
}

.wrap {
	width: 100%;
	height: 100vh;
}
.center {
	margin: 0 auto;
	width: 90%;
	max-width: 1700px;
	min-width: 1500px;
}
